* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: 'Lato', sans-serif;
}

.Navigations {
  position: fixed;
  top: 0;
  width: 100%;
  z-index: 999;
}

.SideBar {
  background: transparent;
  height: 60px;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: start;
          justify-content: start;
  -webkit-align-items: center;
          align-items: center;
}

.Sticky {
  background: #183241;
}

.MenuBar {
  font-size: 2rem;
  background: none;
  color: white;
  padding-right: 1rem;
  display: none;
}

.Hide-Able-Menu {
  display: -webkit-flex;
  display: flex;
  list-style: none;
}

.To-Show-Menu {
  display: inline;
}

.menu-text {
  padding: 20px 20px 20px 20px;
  -webkit-justify-content: start;
          justify-content: start;
  list-style: none;
}

.menu-text a {
  padding: 10px 20px;
  border-radius: 25px;
  color: white;
  transition: 125ms;
  text-decoration: none;
}

.menu-text a:hover {
  background-color: #e66767;
  transition: 125ms;
  color: white;
}

@media screen and (max-width: 680px) {
  .Hide-Able-Menu {
    display: none;
  }

  .MenuBar {
    display: inline;
    margin-left: 30px;
  }

  .To-Show-Menu .menu-text {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: start;
            justify-content: start;
    color: white;
    padding: 8px 0px 8px 16px;
  }

  .To-Show-Menu .menu-text a {
    text-decoration: none;
    color: white;
    font-size: 18px;
    width: 95%;
    height: 100%;
    -webkit-align-items: center;
            align-items: center;
    border-radius: 25px;
    transition: 125ms;
  }
}

.nav-menu {
  /* background-color: #060b26; */
  background: transparent;
  width: 250px;
  height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  position: fixed;
  top: 0;
  left: -100%;
  transition: 850ms;
}

.nav-menu.active {
  left: 0;
  transition: 425ms;
}

.nav-menu.active.Sticky {
  background: #183241;
}

.nav-menu-items {
  width: 100%;
  margin-right: 20px;
}

.nav-menu-items span {
  margin-right: 10;
}

